import React, { useEffect, useState } from "react";
import { Row, Col, Input, Card, Pagination, Spin, Popconfirm } from "antd";
import { Table, Space } from "antd";
import { RiErrorWarningLine } from "react-icons/ri";
import { connect } from "react-redux";
import MasterPageTitle from "../../../../layout/components/content/master-page-title";
import AddFirm from "./addFirm";
import {
  getFirm,
  firmEdit,
  firmDelete,
  isModelVisible,
  SetFirmParams,
} from "../../../../store/Masters/Firm/firmSlice";

function Firm(props) {
  useEffect(() => {
    props.getFirm({ page: 1, search: "", page_size: 10 });
  }, []);
  const { Search } = Input;
  const columns = [
    {
      title: "S.No",
      dataIndex: "index",
      width: "5%",
      // sorter: (a, b) => a.index - b.index,
    },
    {
      title: "Code",
      dataIndex: "code",
      // sorter: (a, b) => a.name.length - b.name.length,
      filterSearch: true,
      width: "35%",
    },
    {
      title: "Firm Name",
      dataIndex: "name",

      filterMode: "tree",
      filterSearch: true,
      // sorter: (a, b) => a.name.length - b.name.length,
      width: "40%",
    },
    {
      title: "Actions",
      key: "action",
      width: "10%",
      render: (row, record) => (
        <Space size="middle">
           {props.userAccessList?.indexOf("Masters.change_firm") !== -1 && (
            <i
              className="ri-edit-box-line hp-mr-2 "
              style={{ cursor: "pointer", color: "#fc6f03" }}
              onClick={() => {
                props.firmEdit(row);
                props.isModelVisible(true);
              }}
            ></i>
           )}

        {props.userAccessList?.indexOf("Masters.delete_firm") !== -1 && (
          <Popconfirm
            placement="topRight"
            title="Are you sure to delete this Record?"
            onConfirm={() => props.firmDelete(record.id)}
            okText="Yes"
            cancelText="No"
            className="hp-ml-8"
            icon={
              <RiErrorWarningLine className="remix-icon hp-text-color-primary-1" />
            }
          >
            <i
              className="ri-delete-bin-2-line "
              style={{ cursor: "pointer", color: "#ff0000" }}
            ></i>
          </Popconfirm>
        )}
        </Space>
      ),
    },
  ];

  const data = props.firmlist.map((row, key) => ({
    ...row,
    index: (props.firmParams.page - 1) * props.firmParams.page_size + key + 1,
  }));

  function onPageChange(page, pageSize) {
    props.SetFirmParams({
      ...props.firmParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
    props.getFirm({
      ...props.firmParams,
      page_size: pageSize,
      page: page == 0 ? 1 : page,
    });
  }

  function onSearch(val) {
    props.SetFirmParams({ ...props.firmParams, search: val });
    props.getFirm({ ...props.firmParams, search: val });
  }
  function showTotal(total) {
    return "Total " + total + " items";
  }
  return (
    <Row gutter={[32, 32]} className="hp-mt-32">
      <MasterPageTitle
        pageTitle="Firm"
        addModelTile={
          props.userAccessList?.indexOf("Masters.add_firm") !== -1 &&
          "Add Firm"
        }
        goBack={true}
        setIsModalVisible={props.isModelVisible}
        isModalVisible={props.model}
      />

      <Col span={24}>
        <Card>
          <Row className="hp-mb-12">
            <Col span={12} xs={24} sm={24} md={14} className="hp-mb-12">
              <Search
                placeholder="Search..."
                allowClear
                onChange={(e) => {
                  if (e.target.value == "") {
                    props.getFirm({
                      ...props.firmParams,
                      page: 1,
                      search: "",
                    });
                    props.SetFirmParams({
                      ...props.firmParams,
                      page: 1,
                      search: "",
                    });
                  }
                }}
                onSearch={(value) => {
                  onSearch(value);
                }}
              />
            </Col>

            <Col span={24}>
              <Spin tip="Loading..." spinning={props.loading} delay={500}>
                <Table
                  columns={columns}
                  dataSource={data}
                  scroll={{ x: 500 }}
                  pagination={false}
                  size="middle"
                />
              </Spin>
            </Col>
            <Col span={24}>
              <Pagination
                size="small"
                current={props.firmParams?.page}
                pageSize={props.firmParams?.page_size}
                total={props.listCount}
                showTotal={showTotal}
                showSizeChanger
                onChange={onPageChange}
                className="hp-mb-12 hp-mt-24 hp-float-right"
              />
            </Col>
          </Row>
        </Card>
      </Col>
      <AddFirm />
    </Row>
  );
}

function mapStateToProps({ firm, system }) {
  return {
    firmlist: firm.firmlist,
    noofpages: firm.noofpages,
    listCount: firm.listCount,
    firmParams: firm.firmParams,
    loading: firm.loading,
    userAccessList: system.userAccessList,
  };
}

export default connect(mapStateToProps, {
  getFirm,
  firmEdit,
  firmDelete,
  isModelVisible,
  SetFirmParams,
})(Firm);
